<template>
  <div class="settings">
    <label class="setting" for="bpm"
      >bpm
      <input
        type="range"
        id="bpm"
        name="bpm"
        min="30"
        max="240"
        step="10"
        v-model="bpm"
        @change="emitSettings"
      />
    </label>
    <p class="setting-value">{{ bpm }}</p>
  </div>
</template>

<script>
export default {
  name: "Settings",
  props: ["initialBpm"],
  data() {
    return {
      bpm: this.initialBpm,
    };
  },
  emits: ["settingsChange"],
  methods: {
    emitSettings() {
      this.$emit("settingsChange", this.bpm);
    },
  },
};
</script>

<style scoped>
.settings {
  padding: var(--spacing);
}

.setting {
  display: flex;
  align-items: center;
}

.setting input {
  margin-left: var(--spacing);
}

.setting-value {
  margin: 0;
  padding: 0;
  text-align: center;
}
</style>
